:root {
    --seo-lite--light: #fff;
    --seo-lite--light-grey: #dfe0ef;
    --seo-lite--dark-grey: #8f90b0;
    --seo-lite--dark: #252634;
    --seo-lite--grey: #ccc;
    --seo-lite--light-background: #f7f7fb;
    --seo-lite--success: #ceffdd;
    --seo-lite--warning: #ffd7ba;
    --seo-lite--error: #ffadad;
    --seo-lite--google-title: #1a0dab;
}

.dark-theme, body[data-theme=dark] {
    --seo-lite--light: #252634;
    --seo-lite--light-grey: #383838;
    --seo-lite--dark-grey: #8f90b0;
    --seo-lite--dark: #fff;
    --seo-lite--grey: #6a6a6a;
    --seo-lite--success: #206434;
    --seo-lite--warning: #a96331;
    --seo-lite--error: #924141;
    --seo-lite--google-title: #6051ff;
    --seo-lite--light-background: #252634;
}

.panel.theme--ee5 {
    background-color: var(--seo-lite--light);
    padding: 15px;
    border: 1px solid var(--seo-lite--grey);
    overflow: auto;
}

.panel.theme--ee5 .button--secondary {
    background-color: #247aa2;
}

.panel.theme--ee5 .panel-heading {
    margin-bottom: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid var(--seo-lite--light-grey);
}

.panel.theme--ee5 .panel-heading .title-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.panel.theme--ee5 .panel-intro {
    color: gray;
    display: block;
    font-size: 12px;
    font-style: normal;
    margin-top: 2px;
}

.c-upload-image {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.upload-image__preview {
    max-width: 300px;
    border: 1px solid var(--seo-lite--dark);
    margin-bottom: 15px;
}

.audit__status.has--detail {
    vertical-align: top;
}

.audit__status.has--detail .audit__status-item {
    width: 100%;
    margin-bottom: 5px;
    border-radius: 30px 3px 3px 30px;

    &:last-child {
        margin-bottom: 0;
    }
}

.audit__status.has--detail .audit__status-item::after{
    opacity: 1;
    position: relative;
    width: 100%;
}

.audit__status.has--detail .audit__status-item::before {
    content: "\f071";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    color:var(--seo-lite--dark);
    margin-right: 10px;
    margin-left: 8px;
}

.audit__status.has--detail .audit__status-item.is--full::before {
    content: "\f058";
} 

.audit__status.has--detail .audit__status-item.is--info::before {
    content: "\f05a";
} 


.audit__status-item {
    padding: 5px;
    margin-bottom: 3px;
    min-width: 25px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 3px;
    color: #000;
    background-color: var(--seo-lite--error);
    position: relative;

    &:last-child {
        margin-bottom: 0;
    }
}

.audit__status-item::after {
    content: 'No ' attr(data-name) ' added';
    position: absolute;
    left: -1px;
    white-space: nowrap;
    bottom: 50%;
    background-color: var(--seo-lite--light);
    padding: 5px;
    border-radius: 3px;
    opacity: 0;
    pointer-events: none;
    z-index: 100;
    transition: ease 300ms;
    transition-property: bottom, opacity;
}

.audit__status-item:hover::after {
    bottom: 100%;
    opacity: 1;
}

.audit__status-item.is--full {
    background-color: var(--seo-lite--success);
    color: #000;
}

.audit__status-item.is--info {
    background-color: var(--seo-lite--grey);
    color: #000;
}

.audit__status-item.is--full::after {
    content: attr(data-name) ' added';
}

.audit__status.has--detail .audit__status-item::after{
    content: attr(data-name);
    white-space: normal;
    color: var(--seo-lite--dark);
}

.is--loading {
    background-image: url(../img/loading.svg);
    width: 100%;
    min-height: 100px;
    display: block;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

.pagination {
    width: 100%;
}

.pagination .filter-bar {
    display: none;
}

.audit__buttons {
    text-align: right;
}

.audit__id {
    white-space: nowrap;
}

.audit__language {
    padding: 5px;
    border: 1px solid;
    border-radius: 3px;
    margin-left: 5px;
}

.button__icon {
    margin-left: 5px;
}

.title-bar__extra-tools {
    margin-bottom: 0;
}

.panel-inner {
    border-bottom: 1px solid var(--seo-lite--light-grey);
    padding-bottom: 15px;
    margin-bottom: 15px;
}

.panel-inner:last-child {
    padding-bottom: 0;
    margin-bottom: 0;
    border-bottom: none;
}

.panel-intro {
    font-size: 14px;
    font-style: italic;
    color: var(--seo-lite--dark-grey);
}

.c-google-audit {
    display: flex;
    flex-direction: column;
    max-width: 524px;
    border: 1px solid var(--seo-lite--light-grey);
    background-color: var(--seo-lite--light);
    border-radius: 6px;
    padding: 15px 0;
}

.google-audit__link,
.google-audit__heading,
.google-audit__description {
    padding-left: 15px;
    padding-right: 15px;
}

.google-audit__link {
    font-size: 14px;
    line-height: 1.3;
}

.google-audit__heading {
    color: var(--seo-lite--google-title);
    font-size: 20px;
    font-weight: 400;
    line-height: 26px;
    padding-top: 5px;
    margin-bottom: 3px;
    position: relative;
}

.google-audit__description {
    font-size: 14px;
    min-height: 20px;
    line-height: 1.3;
    position: relative;
}

.c-facebook-audit {
    display: flex;
    flex-direction: column;
    max-width: 524px;
    border: 1px solid var(--seo-lite--light-grey);
    border-radius: 6px;
    background-color: var(--seo-lite--light-background);
}

.facebook-audit__image-src {
    height: 274px;
    width: 100%;
    border-radius: 6px 6px 0 0;
    object-fit: cover;
}

.facebook-audit__link {
    color: #606770;
    font-size: 12px;
    line-height: 11px;
    text-transform: uppercase;
}

.facebook-audit__description {
    color: #606770;
    font-size: 14px;
    line-height: 20px;
    max-height: 80px;
    position: relative;
}

.facebook-audit__description-inner {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: block;
    -webkit-line-clamp: 1;
}

.facebook-audit__image {
    position: relative;
}

.facebook-audit__body {
    padding: 10px 0;
}

.facebook-audit__heading {
    font-weight: 600;
    overflow: hidden;
    font-size: 16px;
    line-height: 20px;
    margin: 3px 0 0;
    padding-top: 2px;
}

.facebook-audit__link,
.facebook-audit__heading,
.facebook-audit__description {
    padding-left: 12px;
    padding-right: 12px;

}

.c-twitter-audit {
    display: flex;
    position: relative;
    flex-direction: column;
    max-width: 438px;
    border: 1px solid var(--seo-lite--light-grey);
    border-radius: 12px;
    background-color: var(--seo-lite--light);
}

.c-twitter-audit.type--0 .twitter-audit__image,
.c-twitter-audit.type--2 .twitter-audit__image {
    height: 125px;
    position: static;
}

.c-twitter-audit.type--0 .twitter-audit__image-src,
.c-twitter-audit.type--2 .twitter-audit__image-src {
    border-radius: 12px 0 0 12px;
    height: 125px;
    width: 125px;
    min-width: 125px;
}

.c-twitter-audit.type--0 .twitter-audit__body,
.c-twitter-audit.type--2 .twitter-audit__body {
    width: calc(100% - 125px);
}

.c-twitter-audit.type--0,
.c-twitter-audit.type--2  {
    flex-direction: row;
}

.twitter-audit__image-src {
    height: 220px;
    width: 100%;
    border-radius: 12px 12px 0 0;
    object-fit: cover;
}

.twitter-audit__link {
    color: #606770;
    font-size: 14px;
    margin-top: 5px;
    color: #8899A6;
    line-height: 11px;
}

.twitter-audit__description {
    color: #606770;
    font-size: 14px;
    line-height: 10px 18px;
    max-height: 80px;
    position: relative;
}

.twitter-audit__description-inner {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: block;
    -webkit-line-clamp: 1;
}

.twitter-audit__image {
    position: relative;
}

.twitter-audit__body {
    padding: 10px 0;
    width: 100%;
}

.twitter-audit__heading {
    font-weight: 600;
    overflow: hidden;
    font-size: 16px;
    line-height: 20px;
    margin: 3px 0 0;
    padding-top: 2px;
}

.twitter-audit__heading,
.twitter-audit__description,
.twitter-audit__link {
    padding-left: 12px;
    padding-right: 12px;
}

.has--info::before {
    content: '';
    left: calc(100% + 22px);
    bottom: 7px;
    width: 5px;
    height: 5px;
    background-color: var(--seo-lite--light);
    display: block;
    position: absolute;
    z-index: 1;
    border-radius: 100px;
}

.has--info::after {
    content: attr(data-info);
    line-height: 1;
    position: absolute;
    background-color: var(--seo-lite--light-grey);
    border-radius: 10px 3px 3px 10px;
    font-size: 10px;
    left: calc(100% + 15px);
    padding: 5px 10px 5px 20px;
    color: var(--seo-lite--dark);
    white-space: nowrap;
    bottom: 0;
}
.info--error::after {
    background-color: var(--seo-lite--error);
}
.info--success::after {
    background-color: var(--seo-lite--success);
}
.info--warning::after {
    background-color: var(--seo-lite--warning);
}

.c-languages {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: -20px;
    margin-bottom: 20px;
}

.languages__language {
    margin-right: 10px;
    padding: 5px 10px;
    border-bottom: 0;
    border-radius: 0 0 5px 5px;
    background-color: var(--ee-button-secondary-bg);
    
    &.is--active {
        font-weight: bold;
    }
}

.languages__language:last-child {
    margin-right: 0;
}


.headings__heading {
    border-radius: 30px;
    color: var(--ee-button-secondary-color);
    background-color: var(--ee-button-secondary-bg);
    padding: 5px 15px;
    position: relative;

}

.headings__heading::before {
    content: '';
    height: 42px;
    position: absolute;
    width: 2px;
    bottom: 14px;
    left: -15px;
    background-image: linear-gradient(0deg, var(--ee-button-secondary-bg) 60%, rgba(255,255,255,0) 95%);
}
.headings__heading::after {
    content: '';
    position: absolute;
    width: 15px;
    height: 2px;
    background-color: var(--ee-button-secondary-bg);
    left: -15px;
    top: 12px;
}

.headings__heading.is--H1::before {
    display: none;
}

.headings__heading.is--H2 {
    left: 15px;
}
.headings__heading.is--H3 {
    left: 30px;
}
.headings__heading.is--H4 {
    left: 45px;
}
.headings__heading.is--H5 {
    left: 60px;
}
.headings__heading.is--H6 {
    left: 75px;
}

.audit tbody > tr:nth-child(odd) {
    background-color: var(--seo-lite--light-background);
}

.audit {
    height: 100%;
}

.page-speed__inner {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;
}

.page-speed__screenshot-screen {
    padding: 20px 5px 5px 5px;
    background-color: var(--seo-lite--light);
    border: 1px solid var(--ee-tab-bar-border);
    border-radius: 10px;
    position: relative;

    &::before {
        content: '...';
        position: absolute;
        top: -50px;
        font-size: 53px;
        left: 7px;
        opacity: 0.3;
        letter-spacing: -3px;
    }
}

.page-speed__screenshot-screen-inner {
    max-height: 250px;
    overflow: auto;
}

.page-speed__performance-results {
    display: flex;
    justify-content: center;
    position: relative;
    margin-top: 70px;

    &.is--error {
        .page-speed__performance-icon {
            color: var(--seo-lite--dark);
            background-color: var(--seo-lite--error);
        }
    }

    &.is--warning {
        .page-speed__performance-icon {
            color: var(--seo-lite--dark);
            background-color: var(--seo-lite--warning);
        }
    }

    &.is--success {
        .page-speed__performance-icon {
            color: var(--seo-lite--dark);
            background-color: var(--seo-lite--success);
        }
    }
}

.page-speed__performance-icon {
    position: absolute;
    bottom: 100%;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--ee-button-secondary-color);
    background-color: var(--ee-button-secondary-bg);
    border-radius: 50%;
    font-size: 21px;
    margin-bottom: 10px;

    &::before {
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        content: "\f46d";
        color: var(--seo-lite--dark);
        margin-right: 10px;
        margin-left: 8px;
    }
}

.page-speed__performance-score {
    font-weight: 800;
    font-size: 32px;
    margin-right: 5px;

    &::after {
        content: '/';
        margin-left: 5px;
        opacity: 0.4;
    }
}

.page-speed__performance-total {
    font-weight: 800;
    font-size: 32px;
    opacity: 0.4;
}

.page-speed__performance-meta {
    display: flex;
    justify-content: space-around;
    margin-top: 40px;
}

.page-speed__performance-meta-item {
    display: flex;
    align-items: center;

    &::before {
        content: '';
        width: 10px;
        height: 10px;
        margin-right: 5px;
        border-radius: 50%;
        display: block;
    }

    &.is--error {
        &::before {
            background-color: var(--seo-lite--error);
        }
    }

    &.is--warning {
        &::before {
            background-color: var(--seo-lite--warning);
        }
    }

    &.is--success {
        &::before {
            background-color: var(--seo-lite--success);
        }
    }
}

.page-speed__block {
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 30px;
    width: 100%;

    &.is--half {
        width: 50%;
    }
}

.page-speed__footer {
    text-align: center;
    width: 100%;
}

.page-speed__statistics-item {
    display: flex;
}

.page-speed__statistics-item-info {
    margin-left: auto;
    position: relative;

    &:hover {
        &:after {
            visibility: visible;
            opacity: 1;
        }
    }

    &:before {
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        content: "\f05a";
        color: var(--seo-lite--dark);
    }

    &::after {
        content: attr(data-info);
        opacity: 0;
        visibility: hidden;
        position: absolute;
        position: absolute;
        bottom: 100%;
        right: 100%;
        background-color: var(--seo-lite--light);
        padding: 15px;
        border-radius: 5px;
        border: 1px solid var(--seo-lite--light-grey);
    }
}

.theme--ee7 {
    .audit__status.has--detail .audit__status-item::before,
    .page-speed__performance-icon::before,
    .page-speed__statistics-item-info::before {
        font-family: "Font Awesome 6 Pro";
        font-weight: 300;
    }
}